body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
/* header */
.header {
    display: -webkit-flex;
    display: flex;
    border-bottom: 2px solid #333333;
    padding-bottom: 10px;
  }
  .header-qrcode {
    -webkit-flex-basis: 130px;
            flex-basis: 130px;
  }
  .header-contact {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding-left: 18px;
  
    font-style: normal;
  }
  .header-title {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;
  }
  /* header detail */
  .header-qrcode > img {
    width: 130px;
    height: 130px;
  }
  .header-title > h1 {
    margin: 5px 0px;
    line-height: 1;
  }
  .header-title > h3 {
    margin: 10px 0px;
  }
  .header-contact > ul {
    display: block;
    list-style: none;
  }
  .header-contact > ul > li {
    display: block;
    list-style: none;
    line-height: 1.4;
    font-size: 18px;
  }
  .header-contact > ul > li > i {
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    font-size: 18px;
  }
  
  #image-canvas{
    position: relative;
  }
footer{
    text-align: center;
    
}
.row{
    margin-left: -0.1px;
    margin-right: -0.1px;
    overflow: hidden;
    margin-top: 10px;
}
.col-6{
    
    width: 50%;
    float: left;
    padding: 15px;
}
/* preset css */
* {
    box-sizing: border-box;
  }
  html,
  body {
    margin: 0;
    padding: 0;
  }
  body {
    color: #333333;
    background-color: #e0e0e0;
    font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    -webkit-print-color-adjust: exact;
  }
  h1 {
    font-size: 80px;
  }
  h2 {
    font-size: 22px;
  }
  h3 {
    font-size: 20px;
  }
  h4 {
    font-size: 16px;
  }
  h5 {
    font-size: 16px;
  }
  h6 {
    font-size: 14px;
  }
  ul,
  ol,
  figure {
    padding: 0px;
    margin: 0px;
  }
  strong {
    color: #008080;
  }
  
  /* main styles */
  /* a4: 595, 842*/
  .container {
    position: relative;
    background-color: #fff;
    box-shadow: 3px 3px 9px rgba(0, 0, 0, 0.2);
    width: 900px;
    height: 1273px;
    margin: 10px auto;
    padding: 80px 55px;
  }
  
