/* header */
.header {
    display: flex;
    border-bottom: 2px solid #333333;
    padding-bottom: 10px;
  }
  .header-qrcode {
    flex-basis: 130px;
  }
  .header-contact {
    flex: 1;
    padding-left: 18px;
  
    font-style: normal;
  }
  .header-title {
    flex: 1;
    text-align: right;
  }
  /* header detail */
  .header-qrcode > img {
    width: 130px;
    height: 130px;
  }
  .header-title > h1 {
    margin: 5px 0px;
    line-height: 1;
  }
  .header-title > h3 {
    margin: 10px 0px;
  }
  .header-contact > ul {
    display: block;
    list-style: none;
  }
  .header-contact > ul > li {
    display: block;
    list-style: none;
    line-height: 1.4;
    font-size: 18px;
  }
  .header-contact > ul > li > i {
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
    font-size: 18px;
  }
  
  #image-canvas{
    position: relative;
  }